import React from 'react';
import { withRouter } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { fetchLessonData, refreshChessState } from '../../../../redux/ducks/chess';
import './Lesson.scss';

const Lesson = ({ lesson, level, history }) => {
  const dispatch = useDispatch();
  const { name, idx } = lesson;

  const clickHandler = () => {
    dispatch(refreshChessState());
    dispatch(fetchLessonData(level, idx));
    history.push(`/dashboard/${level}`);
  };

  return (
    <li className="lesson__li">
      <img src="https://i.ibb.co/HKrLS0V/feature-lingot.png" alt="photo" className="li__img" />
      <p onClick={clickHandler} className="li__title">{name}</p>
    </li>
  )
};

export default withRouter(Lesson);