import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { checkInputState, checkUserName } from '../../../utils/utils';
import { setInputFieldsState } from '../../../redux/ducks/authentication';
import './UserName.scss';

const UserName = ({ inputFieldsState, setInputFieldsState }) => {
    const { userName } = inputFieldsState;

    useEffect(() => {
        setInputFieldsState({
            userName: null,
            password: null
        });
    }, []);

    const onChangeUserNameHandler = (evt) => {
        if (checkUserName(evt.target.value)) {
            setInputFieldsState({
                ...inputFieldsState,
                userName: evt.target.value,
            });
        } else {
            setInputFieldsState({
                ...inputFieldsState,
                userName: false,
            });
        }
    };

    return (
        <div className="form-group">
            <label>Имя пользователя</label>
            <input onChange={onChangeUserNameHandler} type="text" className="Signin__input" id='name' name="username" />
            <label class={`Signin__label ${checkInputState(userName)}`}>Имя пользователя содержит недопустимые символы</label>
        </div>
    )
};

export default connect((state) => ({
    inputFieldsState: state.authenticationReducer.inputFieldsState
}), (dispatch) => ({
    setInputFieldsState: (state) => dispatch(setInputFieldsState(state))
}))(UserName);