import React from 'react';
import './FastAuth.scss';

const FastAuth = () => {
  return (
    <span className="btns">
        <div className='fast__auth'>
            <i className="fab fa-facebook-f"></i>
            <input type="submit" className='btn btn__facebook' value="Facebook" />
        </div>
        <div className='fast__auth'>
            <i className="fab fa-google"></i>
            <input type="submit" className='btn btn__google' value="Google" />
        </div>
    </span> 
  );
};

export default FastAuth;