import React, { useState, createRef } from 'react';
import { checkUserEmail, checkUserName, chekUserPassword, saveTokenToLocalStorage, getTokenFromLocalStorage, checkInputState } from '../../../utils/utils';
import { signin } from '../../../services/authentication';
import { setAuthenticationState, setInputFieldsState, toSignin } from '../../../redux/ducks/authentication';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import './Signin.scss';
import '../Auth.scss';
import AuthenticationHeader from '../../../components/AuthenticationHeader/AuthenticationHeader';
import FastAuth from '../../../components/FastAuth/FastAuth';
import Footer from '../../../components/Footer/Footer';
import UserName from '../UserName/UserName';
import Password from '../Password/Password';

const Signin = ({ setAuthenticationState, toSignin, inputFieldsState, history }) => {
    const [isEmail, setEmailState] = useState(null);
    const [isSecondPass, setSecondPassState] = useState(null);
    const [secondPass, setSecondPassValue] = useState('');
    const { userName, password } = inputFieldsState; 

    const onChangeEmailHandler = (evt) => {
        if (checkUserEmail(evt.target.value)) setEmailState(evt.target.value); 
        else setEmailState(false);
    };

    const onChangeSecondPasswordHandler = (evt) => {
        const value = evt.target.value;
        setSecondPassValue(value);
        if (password) {
            if (password === evt.target.value) setSecondPassState(value);
            else setSecondPassState(false);
        }
    };

    const signinClickHandler = () => {
        if (userName && isSecondPass && isEmail && password) {
            toSignin({
                username: userName,
                password: password,
                email:  isEmail
            });
        }
    };

    return (
        <div className="Auth">
            <AuthenticationHeader />
            <div className="window">
                <div className="container">
                    <div className="title">Регистрация</div>
                    <FastAuth />
                    <form action="#">
                        <UserName />
                        <div className="form-group">
                            <label>Электронная почта</label>
                            <input onChange={onChangeEmailHandler} type="email" className="Signin__input" id="email" name="email" />
                            <label className={`Signin__label ${checkInputState(isEmail)}`}>Проверьте правильность ввода элекронного адреса</label>
                        </div>
                        <Password secondPass={{passValue: secondPass, setSecondPassState}} />
                        <div className="form-group">
                            <label>Повторите пароль</label>
                            <input onChange={onChangeSecondPasswordHandler} type="password" className="Signin__input" id='confirmPassword' name="password2" />
                            <label className={`Signin__label ${checkInputState(isSecondPass)}`}>Пароли не совпадают</label>
                        </div> 
                        <button onClick={signinClickHandler} type="button" className="btn btn-primary" id='regist'>Зарегистрироваться</button>
                    </form>
                </div>  
            </div>
            <Footer />
        </div>
    )
}

export default withRouter(connect((state) => ({
    inputFieldsState: state.authenticationReducer.inputFieldsState
}), (dispatch) => ({
    toSignin: (body) => dispatch(toSignin(body)),
    setAuthenticationState: (value) => dispatch(setAuthenticationState(value))
}))(Signin));