import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { INFO_BOARD_FLAGS } from '../../../constants/constants';
import { setExerciseIndex, setFinishedExerciesCounter, setBoardFlag, setIsPiecesDraggble, setFen, setHintOn, setNextExerciseButton } from '../../../redux/ducks/chess';
import './InfoBoard.scss';
import Exerciese from './Exerciese/Exerciese';

import chess from '../chess.utils';

const InfoBoard = () => {
  const dispatch = useDispatch();
  const { data, finishedExerciesCounter, boardFlag, currentExerciesIndex, isHintOn, moveData, currentPiecePgn, fen } = useSelector((state) => state.chessReducer);
  const { exercises, name } = data;
  const test = React.useRef(null);


  const renderBoardContent = (flag) => {
    const { ERRONEOUS, NORMAL, CORRECT, POSSIBLE } = INFO_BOARD_FLAGS;

    const solutionButtonHandler = () => {
      chess.load(exercises[currentExerciesIndex].fen);
      chess.move(exercises[currentExerciesIndex].true_moves[0][1]);
      dispatch(setFen(chess.fen()));
      dispatch(setBoardFlag(CORRECT));
      dispatch(setIsPiecesDraggble(false));
      dispatch(setNextExerciseButton(true));
      if (finishedExerciesCounter === currentExerciesIndex) {
        dispatch(setFinishedExerciesCounter(finishedExerciesCounter + 1));
      }
    }

    const tryMoreButtonHandler = () => {
      chess.load(exercises[currentExerciesIndex].fen);
      dispatch(setFen(chess.fen()));
      dispatch(setBoardFlag(NORMAL));
      dispatch(setIsPiecesDraggble(true));
    }

    const hintButtonHandler = () => {
      dispatch(setHintOn(!isHintOn));
    }

    if (flag === NORMAL) {
      return (
        <>
          <Exerciese />
          <p className={isHintOn ? "InfoBoard__hint" : "InfoBoard__hint InfoBoard__hint--hidden"}>
            <span>{exercises[currentExerciesIndex].hints[0]}</span>
          </p>
            <div className="Chess__buttons">
            <button onClick={hintButtonHandler} className="InfoBoard__hint-button">{isHintOn ? 'Скрыть подсказку' : 'Подсказка'}</button>
            </div>
        </>
      )
    } else if (flag === ERRONEOUS ||  flag === POSSIBLE) {
      return (
        <>
          <p>{flag === ERRONEOUS ? exercises[currentExerciesIndex].wrongs[0] : exercises[currentExerciesIndex].possible_moves[0][1]}</p>
          <div className="Chess__buttons">
            <button className="InfoBoard__try-more-button" onClick={tryMoreButtonHandler}>Заново</button>
            <button onClick={solutionButtonHandler} className="InfoBoard__solution-button">Решение</  button>
          </div>
        </>
      )
    } else if (flag === CORRECT) {
      return (
        <>
        <p>{exercises[currentExerciesIndex].descs[1]}</p>
        </>
      )
    }
  }

  return (
    <div className="InfoBoard">
      <h2 className="InfoBoard__title">Упражнения:</h2>
      {renderBoardContent(boardFlag)}
    </div>
  )
}

export default InfoBoard;