import { signin } from '../../services/authentication';
import { saveTokenToLocalStorage, getTokenFromLocalStorage } from '../../utils/utils';

const SET_AUTHENTICATION = 'authentication/SET_AUTHENTICATION';
const SET_SIGNIN_STATE = 'authentication/SET_SIGNIN_STATE';

const initialState = {
    isAuthenticated: false,
    userData: {},
    inputFieldsState: {
        userName: null,
        password: null,
    }
};

const setAuthenticationState = (value) => ({
    type: SET_AUTHENTICATION,
    isAuthenticated: value
});

const setInputFieldsState = (state) => ({
    type: SET_SIGNIN_STATE,
    inputFieldsState: state
});

const toSignin = (body) => (
    async (dispatch) => {
        const response = await signin(body);
        console.log(response);
        if (response) {
            if (response.token) {
                saveTokenToLocalStorage(response.token);
                getTokenFromLocalStorage();
                dispatch(setAuthenticationState(true));
            }
        }
    }
);

const authenticationReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_SIGNIN_STATE:
            return {
                ...state,
                inputFieldsState: action.inputFieldsState
            }
        case SET_AUTHENTICATION:
            return {
                ...state,
                isAuthenticated: action.isAuthenticated
            }
        default:
            return state;    
    }
};

export default authenticationReducer;
export { setAuthenticationState, toSignin, setInputFieldsState };