import { getLessons } from '../../services/getLessons';
import { INFO_BOARD_FLAGS } from '../../constants/constants';
import data from '.../../../example.json'; // test!s

const REFRESH_STATE = 'chess/REFRESH_STATE' 
,SET_DATA = 'chess/SET_DATA'
,SET_BOARD_FLAG = 'chess/SET_BOARD_FLAG'
,SET_EXERCISE_INDEX = 'chess/SET_EXERCISE_INDEX'
,SET_PIECES_DRAGGBLE = 'chess/SET_PIECES_DRAGBBLE'
,SET_FEN = 'chess/SET_FEN'
,SET_HINT = 'chess/SET_HINT'
,SET_NEXT_EXERCISE_BUTTON = 'chess/SET_NEXT_EXERCISE_BUTTON'
,SET_FINISHED_EXERCISES_COUNTER = 'chess/SET_FINISHED_EXERCISES_COUNTER';


const initialState = {
    data: null,
    boardFlag: INFO_BOARD_FLAGS.NORMAL,
    currentExerciesIndex: 0,
    isPiecesDraggble: true,
    fen: null,
    isHintOn: false,
    isNextExerciseButtonActive: false,
    finishedExerciesCounter: 0
};

const chessReducer = (state = initialState, action) => {
    switch(action.type) {
        case REFRESH_STATE:
            return action.state
        case SET_DATA:
            return {
                ...state,
                data: action.data
            }
        case SET_BOARD_FLAG:
            return {
                ...state,
                boardFlag: action.boardFlag
            }
        case SET_EXERCISE_INDEX:
            return {
                ...state,
                currentExerciesIndex: action.currentExerciesIndex
            }
        case SET_PIECES_DRAGGBLE:
            return {
                ...state,
                isPiecesDraggble: action.isPiecesDraggble
            }
        case SET_FEN:
            return {
                ...state,
                fen: action.fen
            }
        case SET_HINT:
            return {
                ...state,
                isHintOn: action.isHintOn
            }
        case SET_NEXT_EXERCISE_BUTTON:
            return {
                ...state,
                isNextExerciseButtonActive: action.isNextExerciseButtonActive
            }
        case SET_FINISHED_EXERCISES_COUNTER:
            return {
                ...state,
                finishedExerciesCounter: action.finishedExerciesCounter
            }
        default: 
            return state;
    }
};

const refreshChessState = () => ({
    type: REFRESH_STATE,
    state: {
        data: null,
        boardFlag: INFO_BOARD_FLAGS.NORMAL,
        currentExerciesIndex: 0,
        isPiecesDraggble: true,
        fen: null,
        isHintOn: false,
        isNextExerciseButtonActive: false,
        currentPiecePgn: null,
        finishedExerciesCounter: 0
    }
})

const fetchLessonData = (levelId, lessonId) => (
    async (dispatch) => {
        const data = await getLessons(`/api/lessons/levels/${levelId}/${lessonId}`);
        if (data) {
            dispatch({
                type: SET_DATA,
                data: data
            });
        }
    }
);

const setBoardFlag = (flag) => ({
    type: SET_BOARD_FLAG,
    boardFlag: flag
});

const setExerciseIndex = (index) => ({
    type: SET_EXERCISE_INDEX,
    currentExerciesIndex: index
});

const setIsPiecesDraggble = (value) => ({
    type: SET_PIECES_DRAGGBLE,
    isPiecesDraggble: value
});

const setFen = (fen) => ({
    type: SET_FEN,
    fen: fen
});

const setHintOn = (value) => ({
    type: SET_HINT,
    isHintOn: value
});

const setNextExerciseButton = (value) => ({
    type: SET_NEXT_EXERCISE_BUTTON,
    isNextExerciseButtonActive: value
});

const setFinishedExerciesCounter = (value) => ({
    type: SET_FINISHED_EXERCISES_COUNTER,
    finishedExerciesCounter: value,
});

export default chessReducer;
export { fetchLessonData, setBoardFlag, setExerciseIndex, setIsPiecesDraggble, setFen, setHintOn, setNextExerciseButton, setFinishedExerciesCounter, refreshChessState };