import React, { useState, createRef } from 'react';
import './Level.scss';

// тут костыль с access, нужно будет переписать корректный флажок доступа к уровню {access ? "lesson__item level__blocked" : "lesson__item"}

const Level = ({ levelData, children }) => {
  const initialHeight = 140;
  const [currentStyles, setCurrentStyles] = useState({ height: `${initialHeight}px` });
  const [isActive, setActiveState] = useState(false);
  const ulRef = createRef();
  const { level, name } = levelData;

  const buttonClickHandler = () => {
    let styles = { height: `${initialHeight}px` }
    if (!isActive) {
      styles = { height: `${initialHeight + ulRef.current.getBoundingClientRect().height}px` };
    }
    setActiveState(!isActive);
    setCurrentStyles(styles);
  }

  return (
    <div style={currentStyles} className="level">
      <div className="level__flex-wrapper">
        <img className="level__img" alt="photo" src="''" />
        <div className="level__info">
          <p className="level__number">Уровень {level}</p>
          <p className="level__title">{name}</p>
        </div>
        <button onClick={buttonClickHandler} className="level__button" type="button">
          {isActive ? 'Скрыть' : 'Приступить'}
        </button>
      </div>
      <ul ref={ulRef} className="level__list">
        {children}
      </ul>
    </div>   
  )
};

export default Level;