import React, { useEffect, useState } from 'react';
import { getTokenFromLocalStorage } from './utils/utils';
import { connect } from 'react-redux';
import { Route, Switch, Redirect } from 'react-router';
import checkToken from './services/checkToken';
import { setAuthenticationState } from './redux/ducks/authentication';
import './App.scss';
import Loader from './components/Loader/Loader';
import Landing from './pages/Landing/Landing';
import Signin from './pages/Authentication/Signin/Signin';
import Login from './pages/Authentication/Login/Login';
import Dashboard from './pages/Dashboard/Dashboard';
import Chess from './pages/Chess/Chess';

const App = ({ isAuthenticated, setAuthenticationState }) => {
  const [isLoaded, setLoadedState] = useState(false);

  useEffect(async () => {
    getTokenFromLocalStorage();
    if (window.token && !isAuthenticated) {
      const result = await checkToken();
      console.log(result);
      if (result.detail !== "Invalid token.") setAuthenticationState(true); // временно, ускорение разработки
    }
    setLoadedState(true);
  }, []);

  const renderRoutes = () => {
    if (isAuthenticated) {
      return (
        <>
          <Route exact path="/" component={Dashboard} />
          <Route exact path="/dashboard" component={Dashboard} />
          <Route path="/dashboard/:id" component={Chess} />
          <Redirect to="/dashboard" />
        </>
      )
    }
    return (
      <>
        <Route exact path="/" component={Landing} />
        <Route path="/login" component={Login} />
        <Route path="/signin" component={Signin} />
        <Redirect to="/" />
      </>
    )
  }

  return (
    <div className="app">
      {isLoaded ? renderRoutes() : <Loader />}
    </div>
  );
}

export default connect((state) => {
  return {
    isAuthenticated: state.authenticationReducer.isAuthenticated,
  }
}, (dispatch) => ({
  setAuthenticationState: (value) => dispatch(setAuthenticationState(value))
}))(App);