import { getLessons } from '../../services/getLessons';

const SET_DATA = 'dashboard/SET_DATA'; // api/lessons/levels/

const initialState = {
    data: null,
};

const dashboardReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_DATA:
            console.log(action);
            return {
                ...state,
                data: action.data
            }
        default:
            return state;
    }
};

const fetchData = () => (
    async (dispatch) => {
        const data = await getLessons('/api/lessons/levels');
        if (data) {
            dispatch({
                type: SET_DATA,
                data: data
            })
        }
    }
);

export default dashboardReducer;
export { fetchData };