import React from 'react';
import './Landing.scss';
import { NavLink } from 'react-router-dom';
import HeaderLogo from '../../components/HeaderLogo/HeaderLogo';
import Footer from '../../components/Footer/Footer';

const Landing = () => {
  return (
    <div className="Landing">
      <header className="Landing__header">
        <nav className="Landing__nav">
          <div className="Landing-nav__inner">
            <HeaderLogo />
            <a href="#for" className="Landing-nav__link">Для кого</a>
            <a href="#" className="Landing-nav__link">Наши курсы</a>
            <a href="#" className="Landing-nav__link">Блог</a>
            <a href="#" className="Landing-nav__link">8 (800) 555 35 35</a>
            <div className="Landing__btns">
              <NavLink className="nav-link-reset nav-link__left" to="/signin">
                <button className="Landing__btn Landing__btn-start" type='button'>
                  начать бесплатно
                </button>
              </NavLink>
              
              <NavLink className="nav-link-reset nav-link__right" to="/login">
                <button className="Landing__btn Landing__btn-login" type='button'>
                  войти
                </button>
              </NavLink>
            </div>
            <button className="burger" type="button">
              <span className="burger__item">Menu</span>
            </button>
          </div>
        </nav>
      </header>
      <div className="preview">
        <div className="container">
          <img src={require('../../assets/img/Orbit.png')} alt="photo" className="preview__img" />
          <div className="preview__content">
            <p className="preview__txt">Новый подход в обучении шахматам. Учитесь с нами везде. В любое время.</p>
            <NavLink className="nav-link-reset" to="/signin">
              <button className="Landing__btn btn-preview preview__start" type="submit">
                Начать
              </button>
            </NavLink>
            <NavLink className="nav-link-reset" to="/login">
              <button className="Landing__btn btn-preview preview__acc" type='submit'>
                У меня уже есть аккаунт
              </button>
            </NavLink>
          </div>
        </div>
      </div>
      <div className="Landing-page">
        <div className="container container__after">
          <div className="intro">
            <div className="intro__image--box">
              <img src={require('./img/Comp.png')} alt="photo" className="intro__img" />
            </div>
            <div className="intro__content">
              <div className="intro__title">Лучший способ научиться играть в шахматы</div>
              <div className="intro__text">Учиться с Chesstery весело и интересно. Зарабатывайте очки за правильно выполненные упражнения, решайте задачи на время, повышайте свой уровень.</div>
            </div>
          </div>
        </div>
        <div className="container container__after">
          <div className="games">
            <div className="games__title">Элемент игры в каждом уроке</div>
            <div className="column">
                <div className="games__col">
                  <div className="games__item">
                    <img src={require('./img/feature-speaker.png')} alt="photo" className="games__icon" />
                    <div className="games__item--title">Индивидуальное обучение</div>
                    <div className="games__item--text">Уроки Chesstery адаптируются под ваш стиль занятий. Упражнения разработаны для максимально эффективного развития всех компонентов игры.</div>
                  </div>
                  <div className="games__item">
                    <img src={require('./img/feature-check.png')} alt="photo" className="games__icon" />
                    <div className="games__item--title">Получайте моментальную оценку</div>
                    <div className="games__item--text">Вы сразу увидите, какие задания выполнили правильно. Если вы не выполните правильно, мы быстро покажем вам, как улучшить результаты.</div>
                  </div>
                </div>
                <div className="games__col">
                  <img className='games__laptop' src={require('./img/laptop-features.png')} alt="photo" className="games__img" />
                </div>
                <div className="games__col">
                  <div className="games__item">
                    <img src={require('../../assets/img/feature-lingot.png')} alt="photo" className="games__icon" />
                    <div className="games__item--title">Мотивирующие награды</div>
                    <div className="games__item--text">Зарабатывайте виртуальные монеты, открывайте новые уровни, осваивайте новые тактики, дебюты, эндшпили, гамбиты и наблюдайте за тем, как растут ваши знания.</div>
                  </div>
                  <div className="games__item">
                    <img src={require('./img/feature-streak.png')} alt="photo" className="games__icon" />
                    <div className="games__item--title">Быстрое развитие</div>
                    <div className="games__item--text">Всего 30 минут в день будет достаточно, чтобы играть на уровне 1-го разряда.</div>
                  </div>
                </div>
                <div className="games__col">
                  <div className="games__item">
                    <div className="games__item--title games__title--new">Элемент игры в каждом уроке</div>
                  </div>
                </div>
            </div>
          </div>
        </div>
        <div className="for">
          <a name='for'></a>
          <div className="container container__after">
            <div className="for__item">
              <img src={require('./img/boyEmoji.png')} alt="photo" className="for__img--left" />
              <div className="for__item--content">
                <div className="for__title">Идеально для детского обучения</div>
                <div className="for__text">Сегодня очень много разных секций и кружков. Но правда в том, что все это только отбивает желание учиться шахматам. Сначала ребёнка необходимо увлечь. Наш игровой подход позволяет не просто вызывать интерес у ребёнка, но и полноценно
                  подготавливает будущего чемпиона.</div>
                <NavLink className="nav-link-reset" to="/signin">
                  <button type="button" className="try__it">
                    <i className="far fa-play-circle try__it--icon"></i>
                    <p className="try__it--text">Попробовать бесплатно</p>
                  </button>
                </NavLink>
              </div>
            </div>
          </div>
          <div className="container container__after">
            <div className="for__item">
              <div className="for__item--content">
                <div className="for__title">Индивидуальный подход к обучению</div>
                <div className="for__text">Занятия в секциях, как правило, проходят в группе. Но каждый воспринимает информацию по разному: кому-то нужно больше времени. В группе не учитывают персональные особенности. Chesstery даёт возможность каждому учиться своём темпе
                  по персональному плану обучения.</div>
                  <NavLink className="nav-link-reset" to="/signin">
                  <button type="button" className="try__it">
                    <i className="far fa-play-circle try__it--icon"></i>
                    <p className="try__it--text">Попробовать бесплатно</p>
                  </button>
                </NavLink>
              </div>
              <img src={require('./img/rocket.png')} alt="photo" className="for__img--right" />
            </div>
          </div>
          <div className="container container__after">
            <div className="for__item">
              <img src={require('./img/quality.png')} alt="photo" className="for__img--left" />
              <div className="for__item--content">
                <div className="for__title">Безопасно для зрения</div>
                <div className="for__text">Мы ограничиваем время обучения до 1 часа в сутки. Таким образом снижается нагрузка на зрение, что соответствует рекомендациям Министерства здравоохранения России.</div>
                <NavLink className="nav-link-reset" to="/signin">
                  <button type="button" className="try__it">
                    <i className="far fa-play-circle try__it--icon"></i>
                    <p className="try__it--text">Попробовать бесплатно</p>
                  </button>
                </NavLink>
              </div>
            </div>
          </div>
          <div className="container container__after">
            <div className="for__item">
              <div className="for__item--content">
                <div className="for__title">Программы от профессионалов</div>
                <div className="for__text">Программы обучения составлялись профессиональными педагогами со всего мира. Обучение соответствует международным стандартам.</div>
                <NavLink className="nav-link-reset" to="/signin">
                  <button type="button" className="try__it">
                    <i className="far fa-play-circle try__it--icon"></i>
                    <p className="try__it--text">Попробовать бесплатно</p>
                  </button>
                </NavLink>
              </div>
              <img src={require('./img/book.png')} alt="photo" className="for__img--right for__img--book" />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default Landing;
