import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../../../services/authentication';
import { saveTokenToLocalStorage, getTokenFromLocalStorage } from '../../../utils/utils';
import { setAuthenticationState } from '../../../redux/ducks/authentication';
import './Login.scss';
import '../Auth.scss';
import AuthenticationHeader from '../../../components/AuthenticationHeader/AuthenticationHeader';
import FastAuth from '../../../components/FastAuth/FastAuth';
import UserName from '../UserName/UserName';
import Password from '../Password/Password';
import Footer from '../../../components/Footer/Footer';

const Login = ({ inputFieldsState, setAuthenticationState }) => {
    const [errorMesage, setErrorMessage] = useState('');

    useEffect(() => {
        setTimeout(setErrorMessage, 3000, '');
    }, [errorMesage]);

    const clickHandler = async () => {
        const { userName, password } = inputFieldsState;
        if (userName && password) {
            const response = await login({
                username: userName,
                password,
            });
            // console.log(response);
            const token = response.token;
            if (token) {
                saveTokenToLocalStorage(token);
                getTokenFromLocalStorage();
                setAuthenticationState(true);
                // history.push('/');
            } else {
                setErrorMessage('Пользователь даз нот экзист');
            }
            // console.log(response);
        }
    };

    return (
        <div className="Auth">
            <AuthenticationHeader />
            <div class="window">
                <div class="container">
                    <div class="title">Вход</div>
                    <FastAuth />
                    <form action="#">
                        <UserName />
                        <Password />
                        <p>{errorMesage}</p>
                        <button onClick={clickHandler} type="button" id='btn__login' class="btn btn-primary">Войти</button>
                    </form>
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default withRouter(connect((state) => ({
    inputFieldsState: state.authenticationReducer.inputFieldsState
}), (dispatch) => ({
    setAuthenticationState: (value) => dispatch(setAuthenticationState(value))
}))(Login));