import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setBoardFlag, setIsPiecesDraggble, setExerciseIndex, setHintOn, setFen, setNextExerciseButton } from '../../redux/ducks/chess';
import { INFO_BOARD_FLAGS } from '../../constants/constants';
import './Chess.scss';
import Loader from '../../components/Loader/Loader';
import DashboardHeader from '../../components/DashboardHeader/DashboardHeader';
import Chessboard from './Chessboard/Chessboard';
import InfoBoard from './InfoBoard/InfoBoard';
import ProgressBar from './ProgressBar/ProgressBar';
import Sidebar from './Sidebar/Sidebar';
import chess from './chess.utils';

const Chess = () => {
  const { data, currentExerciesIndex, isNextExerciseButtonActive, boardFlag } = useSelector((state) => state.chessReducer);
  const dispatch = useDispatch();
  const { NORMAL, CORRECT } = INFO_BOARD_FLAGS;

  const renderBoard = () => {
    const { exercises } = data;
    console.log(data);

    const nextExerciesButtonHandler = () => {
      if (isNextExerciseButtonActive && currentExerciesIndex !== exercises.length - 1) {
        console.log('Сработала');
        dispatch(setBoardFlag(NORMAL));
        dispatch(setIsPiecesDraggble(true));
        dispatch(setExerciseIndex(currentExerciesIndex + 1));
        dispatch(setHintOn(false));
        dispatch(setFen(exercises[currentExerciesIndex + 1].fen));
        chess.load(exercises[currentExerciesIndex + 1].fen);
        dispatch(setNextExerciseButton(false));
      }
    }

    const prevExercisesButtonHandler = () => {
      if (currentExerciesIndex !== 0 && boardFlag !== CORRECT) {
        dispatch(setBoardFlag(NORMAL));
        dispatch(setIsPiecesDraggble(true));
        dispatch(setExerciseIndex(currentExerciesIndex - 1));
        dispatch(setHintOn(false));
        dispatch(setFen(exercises[currentExerciesIndex - 1].fen));
        chess.load(exercises[currentExerciesIndex - 1].fen);
        dispatch(setNextExerciseButton(false));
      }
    }

    return (
      <div className="Chess">
        <DashboardHeader />
        <Sidebar />
        <div className="Chess__page">
        <div className="Chess__container">
          <h2 className="Chess__lesson-title">{data.name}</h2>
          <div className="Chess__video-buttons">
            <button className="Chess__watch-history-button">Смотреть историю</button>
            <button className="Chess__watch-lesson-button">Смотреть урок</button>
          </div>
          <div className="Chess__decorative-wrapper">
          <div className="Chess__wrapper">
              <div className="Chess__exercies-section">
                <InfoBoard />
              </div>
              <div className="Chess__chessboard-section">
                <Chessboard />
              </div>
            </div>
            <ProgressBar />
            <div className="Chess__buttons">
              <button onClick={prevExercisesButtonHandler} className={currentExerciesIndex !== 0 && boardFlag !== CORRECT ? "Chess__prev-exercies-button" : "Chess__prev-exercies-button Chess__prev-exercies-button--disabled"}>Предыдущее упражнение</button>
              <button onClick={nextExerciesButtonHandler} className={isNextExerciseButtonActive ? "Chess__next-exercies-button" : "Chess__next-exercies-button Chess__next-exercies-button--disabled"}>Следующее упражнение</button>
            </div>
          </div>  
        </div>          
        </div>
      </div>
    )
  }

  return (
    <div clasName="Chess">
      {data ? renderBoard() : <Loader />} 
    </div>
  )
};

export default Chess;
