import React from 'react';
import './Footer.scss';
import FooterColItem from './FooterColItem/FooterColItem';

const Footer = () => {
  return (
    <div className="Footer">
      <div className="Footer__container">
        <div className="Footer__inner">
          <div className="Footer__col">
            <div className="Footer__col--title">Обучение</div>
            <FooterColItem label={'Курс по шахматам'} />
            <FooterColItem label={'Помощь по задачам'} />
            <FooterColItem label={'Подписки'} />
          </div>
          <div className="Footer__col">
            <div className="Footer__col--title">Обратная связь</div>
            <FooterColItem label={'Статьи'} />
            <FooterColItem label={'Истории Успеха'} />
            <FooterColItem label={'Конкурсы'} />
          </div>
          <div className="Footer__col">
            <div className="Footer__col--title">О нас</div>
            <FooterColItem label={'О Chesstery'} />
            <FooterColItem label={'Контакты'} />
            <FooterColItem label={'Отзывы'} />
            <FooterColItem label={'F.A.Q.'} />
          </div>
          <div className="Footer__col">
            <div className="Footer__col--title">Подписывайтесь</div>
            <div className="Footer__icon--wrapper">
              <img src={require('./assets/vk-brands.svg')} alt="" className="Footer__col--img"/>
              <img src={require('./assets/instagram-brands.svg')} alt="" className="Footer__col--img"/>
              <img src={require('./assets/facebook-square-brands.svg')} alt="" className="Footer__col--img"/>
              <img src={require('./assets/youtube-brands.svg')} alt="" className="Footer__col--img"/>
              <img src={require('./assets/telegram-plane-brands.svg')} alt="" className="Footer__col--img"/>
              <img src={require('./assets/twitter-brands.svg')} alt="" className="Footer__col--img"/>
            </div>
            <div className="Footer__copyright">
              <div className="Footer__copyright--title">&copy; 2020 Chesstery</div>
              <p className="Footer__copyright--text">&laquo;Шахматистами не рождаются&raquo;</p>
            </div>
          </div>
          <div className="Footer__col">
            <div className="Footer__col--title">Партнёры</div>
            <img src={require('./assets/Sberbank.svg')} alt="" className="Footer__partner--img"/>
            <div className="Footer__partner-wrapper">
              <img src={require('./assets/visa 1.svg')} alt="" className="Footer__partner--uniq"/>
              <img src={require('./assets/mc_vrt_pos 1.svg')} alt="" className="Footer__partner--uniq"/>
            </div>
          </div>
        </div>
      </div>
    </div>
    );
};

export default Footer;