import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import './Sidebar.scss';

const Sidebar = () => {
    const levelsData = useSelector((state) => state.dashboardReducer.data);
    const [isSideBarActive, setIsSidebarActive] = useState(false);
    const [isLevelsListOpened, setIsLevelsListOpened] = useState(false);

    const sideBarButtonClickHandler = () => {
        setIsSidebarActive(!isSideBarActive);
    }

    const levelButtonClickHandler = () => {
        setIsLevelsListOpened(!isLevelsListOpened);
    }

    return (
        <>
            <button onClick={sideBarButtonClickHandler} className={isSideBarActive ? "Sidebar__button Sidebar__button--active" : "Sidebar__button"}></button>
            <div className={isSideBarActive ? "Sidebar Sidebar--active" : "Sidebar"}>
                <div className="Sidebar__container">
                    <ul className="Sidebar__levels">
                        {levelsData.map((it) => {
                            const { level, lessons } = it;
                            return (<li className={isLevelsListOpened ? "Sidebar__level Sidebar__level--active" : "Sidebar__level"}>
                                <h3 className="Sidebar__level-title">
                                    <button onClick={levelButtonClickHandler} className="Sidebar__level-button">
                                        <div className="level-button__cursor">
                                            <svg width="20" height="17" viewBox="0 0 20 17" fill="white" xmlns="http://www.w3.org/2000/svg">
                                                <path d="M14.2089 12.71C12.7985 15.1529 12.0933 16.3743 11.1625 16.7656C10.419 17.0781 9.58097 17.0781 8.83745 16.7656C7.90673 16.3743 7.20152 15.1529 5.79112 12.71L2.95055 7.79C1.54014 5.3471 0.83494 4.12565 0.961477 3.124C1.06256 2.32382 1.48159 1.59805 2.12402 1.11041C2.92821 0.5 4.33862 0.5 7.15944 0.5L12.8406 0.5C15.6614 0.5 17.0718 0.5 17.876 1.11041C18.5184 1.59805 18.9374 2.32382 19.0385 3.124C19.1651 4.12565 18.4599 5.3471 17.0494 7.79L14.2089 12.71Z" fill="white" />
                                            </svg>

                                        </div>
                                    </button>
                                    <p>{`Уровень ${level}`}</p>
                                </h3>
                                <ul className="Sidebar__exercieses">
                                    {lessons.map((it) => <li className="Sidebar__exerciese">{it.name}</li>)}
                                </ul>
                            </li>)
                        })}
                    </ul>
                </div>
            </div>
        </>
    )
}

export default Sidebar;