import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { fetchData } from '../../redux/ducks/dashboard';
import Lesson from './Level/Lesson/Lesson';
import Level from './Level/Level';
import Loader from '../../components/Loader/Loader';
import DashboardHeader from '../../components/DashboardHeader/DashboardHeader';
import Footer from '../../components/Footer/Footer';
import './Dashboard.scss';

const Dashboard = ({ fetchData, data }) => {

  const renderLevels = () => {
    // console.log(data);
    return data.map((levelData) => {
      return  <Level levelData={levelData}>
                {levelData.lessons.map((lesson) => {
                    return <Lesson level={levelData.level} lesson={lesson} />;
                })}
              </Level>;
    });
  };

  useEffect(() => {
    if (!data) fetchData();
    console.log(data);
  }, []);

  return (
    <div className="Dashboard">
      <DashboardHeader/>
      <div className="page">
        <div className="container">
          <div className="lk__content">
            <div className="achievements"></div>
            <div className="lessons__list">
              {data ? renderLevels() : <Loader /> }
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default connect((state) => ({
  data: state.dashboardReducer.data,
}), (dispatch) => ({
  fetchData: () => dispatch(fetchData()),
}
))(Dashboard);