import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setBoardFlag, setIsPiecesDraggble, setFen, setNextExerciseButton, setFinishedExerciesCounter } from '../../../redux/ducks/chess';
import { INFO_BOARD_FLAGS } from '../../../constants/constants';
import { createCorrectPgn } from '../chess.utils';
import './Chessboard';
import ChessboardComponent from 'chessboardjsx';

import chess from '../chess.utils';

const Chessboard = () => {
    const { data, isPiecesDraggble, currentExerciesIndex, fen, finishedExerciesCounter } = useSelector((state) => state.chessReducer);
    const dispatch = useDispatch();
    const { exercises } = data;

    const checkPieceMove = ({ sourceSquare, targetSquare, piece }) => {
        const { possible_moves, true_moves } = exercises[currentExerciesIndex];
        const [index, trueMovePgn] = true_moves[0];

        const correctPgn = createCorrectPgn(piece, targetSquare);

        if (correctPgn === trueMovePgn) {
            dispatch(setBoardFlag(INFO_BOARD_FLAGS.CORRECT));
            dispatch(setNextExerciseButton(true));
            if (finishedExerciesCounter === currentExerciesIndex) dispatch(setFinishedExerciesCounter(finishedExerciesCounter + 1));
        } else if (possible_moves.length !== 0 && correctPgn === possible_moves[0][0][1]) dispatch(setBoardFlag(INFO_BOARD_FLAGS.POSSIBLE));
        else dispatch(setBoardFlag(INFO_BOARD_FLAGS.ERRONEOUS));

    }

    const onDrop = (moveData) => {
        const { sourceSquare, targetSquare } = moveData;
        const result = chess.move({ from: sourceSquare, to: targetSquare });

        if (result) {
            dispatch(setIsPiecesDraggble(false));
            dispatch(setFen(chess.fen()));
            checkPieceMove(moveData);
        }
    };

    useEffect(() => {
        chess.load(exercises[currentExerciesIndex].fen);
        dispatch(setFen(chess.fen()));
    }, []);

    return (
        <div className="Chessboard">
            <ChessboardComponent
                draggable={isPiecesDraggble}
                onDrop={onDrop}
                position={fen}
                calcWidth={(screenData) => {
                    const { screenWidth, screenHeight } = screenData;
                    if (screenWidth > 900) {
                        return 520;
                    } else if (screenWidth < 900 && screenWidth > 840) {
                        return 455;
                    } else if (screenWidth < 840 && screenWidth > 770) {
                        return 400;
                    } else if (screenWidth < 770 && screenWidth > 624) {
                        return 335;
                    } else if (screenWidth < 624 && screenWidth > 550) {
                        return 280;
                    } else {
                        return 322;
                    }
                }}
                undo={true}
                lightSquareStyle={{ backgroundColor: "Ivory", boxShadow: 'inset 1px 1px 1px rgba(154, 147, 140, 0.5), 1px 1px 1px rgba(255, 255, 255, 1)' }}
                darkSquareStyle={{ backgroundColor: "BurlyWood", boxShadow: 'inset 1px 1px 1px rgba(154, 147, 140, 0.5), 1px 1px 1px rgba(255, 255, 255, 1)' }}
                pieces={{
                    wK: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/KingWhite.svg')}
                        alt={"wK"}
                    />
                    ),
                    bK: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/KingBlack.svg')}
                        alt={"bK"}
                    />
                    ),
                    wB: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/BishopWhite.svg')}
                        alt={"wB"}
                    />
                    ),
                    bB: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/BishopBlack.svg')}
                        alt={"bB"}
                    />
                    ),
                    wN: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/KnightWhite.svg')}
                        alt={"wN"}
                    />
                    ),
                    bN: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/KnightBlack.svg')}
                        alt={"bN"}
                    />
                    ),
                    wR: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/RookWhite.svg')}
                        alt={"wR"}
                    />
                    ),
                    bR: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/RookBlack.svg')}
                        alt={"bR"}
                    />
                    ),
                    wQ: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/QueenWhite.svg')}
                        alt={"wQ"}
                    />
                    ),
                    bQ: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/QueenBlack.svg')}
                        alt={"bQ"}
                    />
                    ),
                    wP: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/PawnWhite.svg')}
                        alt={"wP"}
                    />
                    ),
                    bP: ({ squareWidth, isDragging }) => (
                    <img
                        style={{
                        width: isDragging ? squareWidth * 1 : squareWidth,
                        height: isDragging ? squareWidth * 1 : squareWidth
                        }}
                        src={require('../assets/PawnBlack.svg')}
                        alt={"bP"}
                    />
                    ),
                }}
            />
        </div>
    )
};

export default Chessboard;