import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { checkInputState, chekUserPassword } from '../../../utils/utils';
import { setInputFieldsState } from '../../../redux/ducks/authentication';
import './Password.scss';

const Password = ({ secondPass, inputFieldsState, setInputFieldsState }) => {
    const { password } = inputFieldsState;

    useEffect(() => {
        setInputFieldsState({
            password: null,
            userName: null,
        });
    }, []);

    const onChangePasswordHandler = (evt) => {
        if (chekUserPassword(evt.target.value)) {
            const value = evt.target.value;
            setInputFieldsState({
                ...inputFieldsState,
                password: value,
            });
            if (secondPass) {
                const { passValue, setSecondPassState } = secondPass;
                console.log(passValue);
                if (passValue === value) setSecondPassState(value);
                else setSecondPassState(false);
            }
        } else setInputFieldsState({
            ...inputFieldsState,
            password: false,
        });
    };

    return (
        <div className="form-group">
            <label>Пароль</label>
            <input onChange={onChangePasswordHandler} type="password" className="Signin__input" id="password" name="password1" />
            <label className="Signin__label check__pass"></label>
            <label className={`Signin__label ${checkInputState(password)}`}></label>
        </div>
    )
};

export default connect((state) => ({
    inputFieldsState: state.authenticationReducer.inputFieldsState
}), (dispatch) => ({
    setInputFieldsState: (state) => dispatch(setInputFieldsState(state))
}))(Password);