import React, { useState } from 'react';
import './DashboardHeader.scss';
import HeaderLogo from '../../components/HeaderLogo/HeaderLogo';

const DashboardHeader = () => {
  const [isVisible, setIsVisible] = useState('');

  // window.addEventListener('scroll', (evt) => {
  //   console.log(evt);
  // });

  return (
    <header className="Dashboard-header">
      <div className="Dashboard-header__inner">
          <HeaderLogo />
          <nav className="Dashboard-nav">
              <div className="Dashboard-nav__money">
                  <img className="Dashboard-nav__link money" src={require('../../assets/img/feature-lingot.png')}/>
                  <div className="Dashboard-nav__link money__count">3</div>
              </div>
              <img className="Dashboard-nav__photo" src={require('../../assets/img/Orbit.png')} alt="photo" />
          </nav>
      </div>
    </header>
  );
};

export default DashboardHeader;