import React from 'react';
import { useSelector } from 'react-redux';
import './ProgressBar.scss';

const ProgressBar = () => {
    const { finishedExerciesCounter, data } = useSelector((state) => state.chessReducer);

    return (
        <div className="ProgressBar">
            <div style={{ width: `${finishedExerciesCounter * (100 / data.exercises.length)}%` }} className="ProgressBar__proggress-flag"></div>
            <span>{`Выполнено ${finishedExerciesCounter} из ${data.exercises.length} упражнений`}</span>
        </div>
    )
};

export default ProgressBar;