import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import './Exerciese.scss';

const Exerciese = () => {
    const { data, finishedExerciesCounter, currentExerciesIndex } = useSelector((state) => state.chessReducer);
    const exercisesList = useRef();
    const exercise = useRef();

    useEffect(() => {
        exercisesList.current.scrollTop = exercise.current.getBoundingClientRect().height * currentExerciesIndex;
    }, [currentExerciesIndex]);

    return (
        <ul ref={exercisesList} className="InfoBoard__exercises">
            {data.exercises.map((it, i) => {
                const { descs } = it;
                if (i < finishedExerciesCounter && i !== finishedExerciesCounter)
                {
                    return (
                        <li key={descs[0]} ref={i === currentExerciesIndex ? exercise : null} className="Exerciese Exerciese--finished">
                            <div className="Exerciese__indicator"></div>
                            <div className="Exerciese__text">{descs[0]}</div>
                        </li>
                    )
                } else if (i <= finishedExerciesCounter)
                {
                    return (
                        <li key={descs[0]} ref={i === currentExerciesIndex ? exercise : null} className="Exerciese">
                            <div className="Exerciese__indicator"></div>
                            <div className="Exerciese__text">{descs[0]}</div>
                        </li>
                    );
                }
            })}
        </ul>
    )
}

export default Exerciese;