const checkUserEmail = (email) => {
    if (/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)) return true;
    return false; 
};

const checkUserName = (name) => {
    if (/[A-z_0-9]+/g.test(name)) return true;
    return false;
};

const chekUserPassword = (password) => {
    //TODO
    if (password) return true;
    return false;
};

const saveTokenToLocalStorage = (token) => {
    localStorage.token = token;
};

const getTokenFromLocalStorage = () => {
    window.token = localStorage.token;
};

const checkInputState = (state) => {
    if (state === null) return null;
    if (state === false) return 'Signin__label--false';
    return 'Signin__label--true';
};

export { checkUserEmail, checkInputState, checkUserName, chekUserPassword, saveTokenToLocalStorage, getTokenFromLocalStorage };